* {
  font-family: 'Roboto', sans-serif;
}

nav {
  height: 50px;
  background: #212121;
}

.menu {
  display: flex;
  justify-content: space-between;
  padding: 13px 70px;
  margin: 0;
  list-style: none;

  .menu_link {
    font-weight: 300;
    font-size: 15px;
    color: #FFFFFF;
    text-decoration: none;
  }

  .menu_item {
    border-right: 2px dotted #000000;
    padding-right: 40px;
  }
}

.subheader {
  position: absolute;
  width: 100%;
  height: 84px;
  background-color: rgba(0, 0, 0, 0.6);

  .subheader_logo {
    display: block;
    max-width: 100%;
    margin-top: 16px;
  }

  .logo_text {
    font-weight: 100;
    font-size: 13px;
    color: #FFFFFF;
    opacity: 0.5;
    margin-top: 6px;
  }
}

.subheader_call {
  margin-top: 18px;
  font-weight: 300;
  font-size: 12px;
  line-height: 14ppx;
  color: #FFFFFF;
  text-align: right;
  opacity: 0.5;
}

.subheader_number {
  display: block;
  margin-top: 8px;
  font-weight: 400;
  font-size: 22px;
  color: #FFFFFF;
  text-transform: uppercase;
  text-align: right;
  line-height: 12px;
}

.subheader_btn {
  border-radius: 8px;
  border-bottom-style: none;
  border: 0px;
  margin-top: 19px;
  width: 172px;
  height: 46px;
  background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
  box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
}

.promo {
  height: 800px;
  background: url(../img/bg/uber_bg.png);
  padding: 150px 0;
}

.promo_text {
  margin: 0;
  font-weight: 900;
  font-size: 60px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  line-height: 60px;
}

.promo_subheader {
  margin: 0;
  margin-top: 22px;
  font-weight: 700;
  font-size: 36px;
  text-transform: uppercase;
  text-align: center;
  color: #ffffff;
  line-height: 36px;
}

.text_subheader {
  margin-top: 16px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
}

.promo_btn {
  display: block;
  margin: 115px auto 0 auto;
  border-radius: 8px;
  border-bottom-style: none;
  border: 0px;
  width: 259px;
  height: 63px;
  background: linear-gradient(0deg, #1EACC7 0%, #4CE2FF 100%);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
  border-radius: 4px;
  font-weight: 400;
  font-size: 18px;
  text-transform: uppercase;
  color: #FFFFFF;
}

.benefits {}

.label {
  display: block;
  margin: 0 auto;
  margin-top: 18px;
  width: 115px;
  height: 27px;
  background: #000000;
  font-weight: 400;
  font-size: 11px;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  padding: 7px;
}

.titel {
  display: block;
  margin-top: 32px;
  font-weight: 700;
  font-size: 38px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #222222;
}

.wrapper_oll {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}
.wrapper1 {
  .round {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 116px;
    height: 116px;
    border-radius: 300px;
    background: #1EACC7;
  }
  padding: 16px;
  display: flex;
  align-items: center;
}
.wrapper_text {
  font-weight: 300;
  font-size: 15px;
  line-height: 21px;
  color: #202020;
  width: 371px;
  height: 150px;
  margin-left: 25px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.wrapper_span {
  font-weight: 400;
  font-size: 17px;
  line-height: 21px;
  color: #1EACC7;
  margin-bottom: -14px;
}

.mobile {
  padding: 48px 0 80px 0;
  max-height:864px ;
  background-image: url(../img/bg/mobile_bg.png);
  &_item {
    width: 320px;
    height: 280px;
    margin: 0 auto;
      &_1 {
        background-image: url(../img/app/app1.png) center (center / cover) no-repeat
      }
  }
  .titel {
    margin-top: 32px;
  }
  .subtitle {
    font-weight: 400;
    font-size: 17px;
    line-height: 21px;
    display: flex;
    align-items: center;
    text-align: center;
    color: #222222;
    margin-top: 10px;
  }
}
